<title></title>

<desc>
    
</desc>

<template>
    <article>
        <Steps :current="current" direction="vertical">
            <Step title="" content=""></Step>
            <Step title="" content=""></Step>
            <Step title="" content=""></Step>
            <Step title="" content=""></Step>
        </Steps>
        <Button type="primary" @click="next">Next step</Button>
    </article>
</template>

<script>
export default {
    data() {
        return {
            current: 1
        }
    },
    methods: {
        next() {
            if (this.current === 3) {
                this.current = 0
            } else {
                this.current += 1
            }
        }
    }
}
</script>
