<title>tooltip</title>

<desc>
    tip-formatterslidertooltip
</desc>

<template>
    <article>
        <p class="props">tipFormatter = (value) => ': ' + value</p>
        <Slider tooltipVisible="always" :tipFormatter="format" />
    </article>
</template>

<script>
export default {
    name: 'TipFormatter',
    methods: {
        format(value) {
            return ': ' + value
        }
    }
}
</script>
