<title></title>

<desc>
    <br />
    Upload
</desc>

<template>
    <Upload
        action="//jsonplaceholder.typicode.com/posts/"
        accept="image/jpeg, image/png"
        :format="format"
        :max-size="15"
        :limit="3"
        :on-error="handleError"
        :on-remove="handleRemove"
        :on-format-error="handleFormatError"
        :on-exceeded-limit="handleExceededLimit"
        :on-exceeded-size="handleExceededSize"
        multiple
    >
        <Button><Icon iconname="cloud-upload"></Icon></Button>
    </Upload>
</template>

<style lang="scss"></style>

<script>
export default {
    data() {
        return {
            format: ['jpg', 'jpeg', 'png']
        }
    },
    methods: {
        handleSuccess(res, file, files) {
            file.url =
                'https://camo.githubusercontent.com/a4ee422ec1accc811276f220561976f14005b13b/687474703a2f2f7a6f762e7468657068656e69782e636e2f696d672f6c6f676f2e35313134623463362e706e67'
        },
        handleError(err, response, file) {
            this.$Message.error({
                title: '',
                text: file.name + '',
                duration: 500,
                closable: false
            })
        },
        handleRemove(file, fileList) {
            this.$Message.info({
                title: '',
                text: file.name + '',
                duration: 500,
                closable: false
            })
        },
        handleFormatError(file) {
            this.$Message.error({
                title: '',
                text: file.name + '',
                duration: 500,
                closable: false
            })
        },
        handleExceededLimit(uploadFiles, fileList) {
            this.$Message.error({
                title: '',
                text: '3',
                duration: 500,
                closable: false
            })
        },
        handleExceededSize(file) {
            this.$Message.error({
                title: '',
                text: '15kb',
                duration: 500,
                closable: false
            })
        }
    }
}
</script>
